import * as d3 from 'd3';
import app from '@/main.js';

export function drawTree(
  selector,
  treeData,
  flag = '0',
  isDbclick = true,
  isContrast = false,
  dateRanges = [],
  callback
) {
  const elem = document.querySelector(selector);
  let svgWidth = elem.clientWidth;
  let svgHeight = elem.clientHeight;
  let ratio = 1;
  let maxLabelLength = 0;
  let totalNodes = 0;

  // 这里我们将svg元素，和子group元素拆分
  const svg = d3
    // 在页面的tree-container里添加svg对象
    .select(selector)
    // 设置svg宽高
    .attr('width', svgWidth)
    .attr('height', svgHeight);

  svg.selectAll('*').remove();

  const view = svg
    // 在svg里添加group元素
    .append('g')
    // 将group放置在左上方
    .attr('transform', 'translate(0, 0)');

  const zoom = d3
    .zoom()
    // 设置缩放区域为0.1-3倍
    .scaleExtent([0.1, 3])
    .on('zoom', () => {
      ratio = d3.event.transform.k;
      // 子group元素将响应zoom事件，并更新transform状态
      view.attr(
        'transform',
        `translate(${d3.event.transform.x}, ${d3.event.transform.y}) scale(${d3.event.transform.k})`
      );
    });

  svg.call(zoom).on('dblclick.zoom', () => {});
  let i = 0;
  const duration = 750;
  let root = null;

  initData(treeData);

  // 数据初始化绑定（包括数据更新）
  function initData() {
    if (!treeData) {
      return;
    }
    root = d3.hierarchy(treeData, d => d.children);
    visit(
      treeData,
      d => {
        totalNodes++;
        maxLabelLength = Math.max(d.name.length, maxLabelLength);
      },
      d => {
        return d.children && d.children.length > 0 ? d.children : null;
      }
    );

    // 设置第一个元素的初始位置
    root.x0 = svgHeight / 2;
    root.y0 = 0;

    // 第二层以上元素收起
    if (Array.isArray(root.children)) {
      root.children.forEach(collapse);
    }

    // 更新节点状态
    updateChart(root);
    centerNode(root);
  }

  // 数据更新绑定
  function updateChart(source) {
    var levelWidth = [1];
    var childCount = function(level, n) {
      if (n.children && n.children.length > 0) {
        if (levelWidth.length <= level + 1) levelWidth.push(0);
        levelWidth[level + 1] += n.children.length;
        n.children.forEach(function(d) {
          childCount(level + 1, d);
        });
      }
    };
    childCount(0, root);
    var newHeight = d3.max(levelWidth) * 250;
    // 定义Tree层级，并设置宽高
    var treemap = d3.tree().size([newHeight, svgWidth]);
    // 设置节点的x、y位置信息
    var treeData = treemap(root);

    // 计算新的Tree层级
    var nodes = treeData.descendants(),
      links = treeData.descendants().slice(1);

    // 设置每个同级节点间的y间距为最大节点名称长度
    nodes.forEach(d => {
      d.y = d.depth * (maxLabelLength * 12);
    });

    // node交互和绘制
    updateNodes(source, nodes);
    // link交互和绘制
    updateLinks(source, links);

    // 为动画过渡保存旧的位置
    nodes.forEach(d => {
      d.x0 = d.x;
      d.y0 = d.y;
    });

    // centerNode(source);
  }
  function updateNodes(source, nodes) {
    // 给节点添加id，用于选择集索引
    var node = view.selectAll('g.node').data(nodes, d => {
      return d.id || (d.id = ++i);
    });

    // 添加enter操作，添加类名为node的group元素
    var nodeEnter = node
      .enter()
      .append('g')
      .attr('class', d => `node node${d.id}`)
      .attr('id', d => d.id)
      // 默认位置为当前父节点的位置
      .attr('transform', `translate(${source.y0}, ${source.x0})`)
      // 添加mouseover事件
      .on('mouseover', d => {
        // 从d3.event获取鼠标的位置
        var transform = d3.event;
        var yPosition = transform.pageY + 10;
        var xPosition = transform.pageX + 10;

        // 将浮层位置设置为鼠标位置
        var chartTooltip = d3
          .select(`#chartTooltip${flag}`)
          .style('left', xPosition + 'px')
          .style('top', yPosition + 'px');
        if (isContrast) {
          let passCount1 = 0;
          let passCount2 = 0;
          //const dateRanges = getChartDateRange(d.data.value);
          try {
            if (d.data.value[dateRanges[0]]) {
              const result1 = d.data.attributes.result[dateRanges[0]];
              let obj1 = result1.find(item => item.result === '通过');
              if (obj1) {
                passCount1 = obj1.count;
              }
            }
            if (d.data.value[dateRanges[1]]) {
              const result2 = d.data.attributes.result[dateRanges[1]];
              let obj2 = result2.find(item => item.result === '通过');
              if (obj2) {
                passCount2 = obj2.count;
              }
            }
          } catch (e) {
            console.log(e);
          }

          // 更新浮层内容
          /*let contrastTitle1s = dateRanges[0].split('&');
          let constrastTitle1 = (contrastTitle1s[0].slice(2) + '&' + contrastTitle1s[1].slice(2)).replace(/-/g,'.');
          let contrastTitle2s = dateRanges[0].split('&');
          let constrastTitle2 = (contrastTitle2s[0].slice(2) + '&' + contrastTitle2s[1].slice(2)).replace(/-/g,'.');*/
          /*chartTooltip.select('.contrastTitle1').text(constrastTitle1);
          chartTooltip.select('.contrastTitle2').text(constrastTitle2);*/
          chartTooltip.select('.contrastCount1').text(d.data.value[dateRanges[0]] ? d.data.value[dateRanges[0]] : 0);
          chartTooltip.select('.contrastCount2').text(d.data.value[dateRanges[1]] ? d.data.value[dateRanges[1]] : 0);
          chartTooltip
            .select('.contrastRate1')
            .text(d.data.value[dateRanges[0]] ? ((passCount1 / d.data.value[dateRanges[0]]) * 100).toFixed(2) : 0);
          chartTooltip
            .select('.contrastRate2')
            .text(d.data.value[dateRanges[1]] ? ((passCount2 / d.data.value[dateRanges[1]]) * 100).toFixed(2) : 0);
        } else {
          let passCount = 0;
          try {
            const result = d.data.attributes.result;
            let obj = result.find(item => item.result === '通过');
            if (obj) {
              passCount = obj.count;
            }
          } catch (e) {
            console.log(e);
          }

          // 更新浮层内容
          chartTooltip.select('.count').text(`总数：${d.data.value}`);
          chartTooltip
            .select('.rate')
            .text(`通过率：${d.data.value ? ((passCount / d.data.value) * 100).toFixed(2) : 0}%`);
        }
        // 移除浮层hidden样式，展示浮层
        chartTooltip.classed('hidden', false);
      })
      // 添加mouseover事件
      .on('mouseout', () => {
        // 添加浮层hidden样式，隐藏浮层
        d3.select(`#chartTooltip${flag}`).classed('hidden', true);
      })
      // 给每个新加的节点绑定click事件
      .on('click', click);

    // 给每个新加的group元素添加cycle元素
    nodeEnter
      .append('circle')
      .attr('class', 'node')
      .attr('r', 1e-6)
      // 如果元素有子节点，且为收起状态，则填充浅蓝色
      .style('fill', function(d) {
        let color = '#aaa';
        if (d._children) {
          color = 'lightsteelblue';
        } else if (d.data.children) {
          color = '#fff';
        }
        return color;
      });

    // 给每个新加的group元素添加文字说明
    let text = nodeEnter
      .append('text')
      .attr('dy', '1.5em')
      .attr('text-anchor', () => 'middle')
      .text(d => {
        if (isContrast) {
          let passCount1 = 0;
          let passCount2 = 0;
          //const dateRanges = getChartDateRange(d.data.value);
          try {
            if (d.data.value[dateRanges[0]]) {
              const result1 = d.data.attributes.result[dateRanges[0]];
              let obj1 = result1.find(item => item.result === '通过');
              if (obj1) {
                passCount1 = obj1.count;
              }
            }
            if (d.data.value[dateRanges[1]]) {
              const result2 = d.data.attributes.result[dateRanges[1]];
              let obj2 = result2.find(item => item.result === '通过');
              if (obj2) {
                passCount2 = obj2.count;
              }
            }
          } catch (e) {
            console.log(e);
          }
          let name = `${d.data.name}<br />总数：<tspan>${
            d.data.value[dateRanges[0]] ? d.data.value[dateRanges[0]] : 0
          }</tspan> , <tspan> ${
            d.data.value[dateRanges[1]] ? d.data.value[dateRanges[1]] : 0
          }</tspan><br />通过率：<tspan>${
            d.data.value[dateRanges[0]] ? ((passCount1 / d.data.value[dateRanges[0]]) * 100).toFixed(2) : 0
          }% </tspan> , <tspan>${
            d.data.value[dateRanges[1]] ? ((passCount2 / d.data.value[dateRanges[1]]) * 100).toFixed(2) : 0
          }%</tspan>${isDbclick ? '<br />详情' : ''}`;
          d.nameArr = name.split('<br />');
        } else {
          let passCount = 0;
          try {
            const result = d.data.attributes.result;
            let obj = result.find(item => item.result === '通过');
            if (obj) {
              passCount = obj.count;
            }
          } catch (e) {
            console.log(e);
          }
          let name = `${d.data.name}<br />总数：${d.data.value}<br />通过率：${
            d.data.value ? ((passCount / d.data.value) * 100).toFixed(2) : 0
          }%${isDbclick ? '<br />详情' : ''}`;
          d.nameArr = name.split('<br />');
        }
      });
    text
      .selectAll('tspan')
      .data(d => d.nameArr)
      .enter()
      .append('tspan')
      .attr('x', 0)
      .attr('dy', '1.5em')
      .attr('text-anchor', () => 'middle')
      .html(function(d) {
        return d;
      });
    text.selectAll('tspan:last-child').on('click', showNodeDetail);

    // 获取update集
    var nodeUpdate = nodeEnter.merge(node);

    // 设置节点的位置变化，添加过渡动画效果
    nodeUpdate
      .transition()
      .duration(duration)
      .attr('transform', d => `translate(${d.y}, ${d.x})`);

    // 更新节点的属性和样式
    nodeUpdate
      .select('circle.node')
      .attr('r', 4.5)
      .style('fill', function(d) {
        let color = '#aaa';
        if (d._children) {
          color = 'lightsteelblue';
        } else if (d.data.children) {
          color = '#fff';
        }
        return color;
      })
      .attr('cursor', 'pointer');

    // 获取exit操作
    var nodeExit = node
      .exit()
      // 添加过渡动画
      .transition()
      .duration(duration)
      .attr('transform', `translate(${source.y}, ${source.x})`)
      // 移除元素
      .remove();

    // exit集中节点的cycle元素尺寸变为0
    nodeExit.select('circle').attr('r', 1e-6);

    // exit集中节点的text元素可见度降为0
    nodeExit.select('text').style('fill-opacity', 1e-6);
  }
  function updateLinks(source, links) {
    // 更新数据
    var link = view.selectAll('path.link').data(links, function(d) {
      return d.id;
    });

    // 添加enter操作，添加类名为link的path元素
    var linkEnter = link
      .enter()
      .insert('path', 'g')
      .attr('class', 'link')
      // 添加id
      .attr('id', d => `${flag}textPath${d.id}`)
      // 默认位置为当前父节点的位置
      .attr('d', d => {
        var o = {
          x: source.x0,
          y: source.y0
        };
        return diagonalReverse(o, o);
      });

    // enter操作中，添加text，同时添加与path匹配的textPath
    link
      .enter()
      .append('text')
      // 给text添加textPath元素
      .append('textPath')
      // 给textPath设置path的引用
      .attr('xlink:href', d => `#${flag}textPath${d.id}`)
      // 字体居中
      .style('text-anchor', 'middle')
      .attr('startOffset', '50%')
      .attr('class', 'linktext')
      .html((d, i) => {
        if (isContrast) {
          //const dateRanges = getChartDateRange(d.data.value);
          return `<tspan>${d.data.value[dateRanges[0]] ? d.data.value[dateRanges[0]] : 0} (${
            d.data.value[dateRanges[0]] && d.parent.data.value[dateRanges[0]]
              ? ((d.data.value[dateRanges[0]] / d.parent.data.value[dateRanges[0]]) * 100).toFixed(2)
              : 0
          }%) </tspan>,
                <tspan>${d.data.value[dateRanges[1]] ? d.data.value[dateRanges[1]] : 0} (${
            d.data.value[dateRanges[1]] && d.parent.data.value[dateRanges[1]]
              ? ((d.data.value[dateRanges[1]] / d.parent.data.value[dateRanges[1]]) * 100).toFixed(2)
              : 0
          }%)</tspan>`;
        } else {
          return `${d.value} (${((d.value / d.parent.value) * 100).toFixed(2)}%)`;
        }
      });

    // 获取update集
    var linkUpdate = linkEnter.merge(link);

    // 更新添加过渡动画
    linkUpdate
      .transition()
      .duration(duration)
      .attr('d', d => diagonalReverse(d, d.parent));

    // 获取exit集
    var linkExit = link
      .exit()
      // 设置过渡动画
      .transition()
      .duration(duration)
      .attr('d', d => {
        var o = {
          x: source.x,
          y: source.y
        };
        return diagonalReverse(o, o);
      })
      // 移除link
      .remove();
  }
  function centerNode(source) {
    let x = -source.y0;
    let y = -source.x0;
    x = x * ratio + 100;
    y = y * ratio + svgHeight / 2;
    d3.select('g')
      .transition()
      .duration(duration)
      .attr('transform', `translate(${x}, ${y}) scale(${ratio})`);
    svg
      .transition()
      .duration(duration)
      .call(zoom.transform, d3.zoomIdentity.translate(x, y).scale(ratio));
  }

  // 当点击时，切换children，同时用_children来保存原子节点信息
  function click(d) {
    if (!isDbclick) {
      if (d.children) {
        d._children = d.children;
        d.children = null;
      } else {
        d.children = d._children;
        d._children = null;
      }
      updateChart(d);
      return;
    }
    if (d._clickid) {
      // 若在300ms里面点击第二次，清空定时器，执行双击事件
      clearTimeout(d._clickid);
      d._clickid = null;
      dbclick(d);
    } else {
      // 首次点击，添加定时器，300ms后进行toggle
      d._clickid = setTimeout(() => {
        if (d.children) {
          d._children = d.children;
          d.children = null;
        } else {
          d.children = d._children;
          d._children = null;
        }
        updateChart(d);
        d._clickid = null;
      }, 300);
    }
  }

  function showNodeDetail() {
    let event = d3.event;
    event.stopPropagation();
    let node = event.target.parentNode;
    let modulename = node.querySelector('tspan').innerHTML;
    console.log(modulename);
    if (callback) {
      callback(modulename);
    }
  }

  function dbclick(d) {
    console.log(d);
    app.$store.commit('UPDATE_TREEDATALIST', [d.data]);
    app.$router.push(`/children-tree?modulename=${d.data.name}`);
  }
}

function visit(parent, visitFn, childrenFn) {
  if (!parent) return;
  visitFn(parent);
  var children = childrenFn(parent);
  if (children) {
    var count = children.length;
    for (var i = 0; i < count; i++) {
      visit(children[i], visitFn, childrenFn);
    }
  }
}

function collapse(d) {
  if (d.children) {
    d._children = d.children;
    d._children.forEach(collapse);
    d.children = null;
  }
}

function diagonal(s, d) {
  let path = `M ${s.y} ${s.x}
              C ${(s.y + d.y) / 2} ${s.x},
                ${(s.y + d.y) / 2} ${d.x},
                ${d.y} ${d.x}`;

  return path;
}
function diagonalReverse(s = {}, d = {}) {
  let path = `M ${d.y} ${d.x}
              C ${(s.y + d.y) / 2} ${d.x},
                ${(s.y + d.y) / 2} ${s.x},
                ${s.y} ${s.x}`;
  return path;
}
